export default {
  name: 'zoomIn',
  props: {
    duration: {
      type: String,
      default: '1s',
    },
    delay: {
      type: String,
      default: '0s',
    },
  },
  computed: {
    animationStyle() {
      return {
        transitionDuration: this.duration,
        transitionDelay: this.delay,
      };
    },
  },
  mounted() {
    const el = this.$refs.animatedElement;
    requestAnimationFrame(() => {
      el.classList.add('animate');
      el.classList.remove('enter-anim');
    });
  },
};
